
.seller {
  position: absolute;
  top: 166px;
  left: 0px;
  bottom: 0px;
  width: 100%;
  overflow: hidden;
}
.seller .head-view {
  padding: 18px;
}
.seller .head-view > h1 {
  margin-bottom: 10px;
  line-height: 16px;
  font-size: 16px;
  color: #07111b;
}
.seller .head-view .desc {
  padding-bottom: 18px;
  font-size: 0px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.seller .head-view .desc .star {
  display: inline-block;
  margin-right: 8px;
  vertical-align: top;
}
.seller .head-view .desc > label,
.seller .head-view .desc span {
  display: inline-block;
  vertical-align: top;
  line-height: 18px;
  font-size: 12px;
  color: #4d555d;
}
.seller .head-view .desc > label {
  margin-right: 12px;
}
.seller .head-view .rmb-time {
  display: flex;
  padding-top: 18px;
}
.seller .head-view .rmb-time .list {
  flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.2);
}
.seller .head-view .rmb-time .list:last-child {
  border: none;
}
.seller .head-view .rmb-time .list > h2 {
  margin-bottom: 8px;
  line-height: 14px;
  font-size: 14px;
  color: #93999f;
}
.seller .head-view .rmb-time .list .block {
  line-height: 24px;
  font-size: 24px;
  color: #07111b;
  font-weight: 300;
}
.seller .head-view .rmb-time .list .block > span {
  font-size: 10px;
}
.seller .head-view .favorite {
  position: absolute;
  top: 18px;
  right: 18px;
  text-align: center;
  transition-delay: all 1s;
/*&.star-move-enter-active
					animation: move-in 1s;  
				&.star-move-leave-active
					animation: move-out 1s;*/
}
.seller .head-view .favorite.star-move-enter-active {
  transform: translate3d(0, 0, 0);
}
.seller .head-view .favorite.star-move-enter,
.seller .head-view .favorite.star-move-leave-active {
  transition-timing-function: ease-in-out;
  transform: translate3d(0, 60px, 0);
  transition-delay: all 0.5s;
}
.seller .head-view .favorite .icon-favorite {
  display: block;
}
.seller .head-view .favorite .favor {
  font-size: 28px;
  line-height: 28px;
  color: #f01414;
}
.seller .head-view .favorite .nofavor {
  font-size: 28px;
  line-height: 28px;
  color: #93999f;
}
.seller .head-view .favorite > span {
  line-height: 14px;
  font-size: 14px;
  color: #07111b;
}
.seller .bulletin {
  padding: 18px 18px 0px 18px;
}
.seller .bulletin > h1 {
  margin-bottom: 10px;
  line-height: 16px;
  font-size: 16px;
  color: #07111b;
}
.seller .bulletin .content {
  padding: 0px 12px 16px 12px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.seller .bulletin .content > p {
  text-align: justify;
  line-height: 24px;
  font-size: 12px;
  color: #f01414;
}
.seller .bulletin .supports > li {
  padding: 18px 12px;
  line-height: 24px;
  font-size: 12px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
}
.seller .bulletin .supports > li:last-child {
  border: none;
}
.seller .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: middle;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.seller .icon.decrease {
  background-image: url("img/decrease_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .icon.decrease {
    background-image: url("img/decrease_4@3x.png");
}
}
.seller .icon.discount {
  background-image: url("img/discount_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .icon.discount {
    background-image: url("img/discount_4@3x.png");
}
}
.seller .icon.special {
  background-image: url("img/special_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .icon.special {
    background-image: url("img/special_4@3x.png");
}
}
.seller .icon.invoice {
  background-image: url("img/invoice_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .icon.invoice {
    background-image: url("img/invoice_4@3x.png");
}
}
.seller .icon.guarantee {
  background-image: url("img/guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .icon.guarantee {
    background-image: url("img/guarantee_4@3x.png");
}
}
.trade-info {
  padding: 18px;
}
h1 {
  margin-bottom: 16px;
  line-height: 16px;
  font-size: 16px;
  color: #07111b;
}
.ul .list {
  padding: 20px 12px;
  font-size: 14px;
  line-height: 18px;
  color: #00a0dc;
  border-top: 1px solid rgba(7,17,27,0.2);
}
.menu-pop .show-menu {
  position: fixed;
  bottom: 60px;
  right: 10px;
  z-index: 10;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  border: none;
  outline: none;
  color: #fff;
  font-size: 18px;
  background: #00a0dc;
}
.menu-pop .menu-box {
  position: fixed;
  bottom: 60px;
  right: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition: all 0.7s ease-in;
}
.menu-pop .menu-box .inner {
  display: inline-block;
  position: absolute;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 50%;
  background: #00c850;
  text-align: center;
  color: #fff;
  transition: all 0.4s;
}
.menu-pop .menu-box .inner-1 {
  top: -50px;
  left: 10px;
}
.menu-pop .menu-box .inner-2 {
  left: -30px;
  top: -30px;
}
.menu-pop .menu-box .inner-3 {
  left: -50px;
  top: 10px;
}
.menu-pop .menu-box .inner-4 {
  left: -30px;
  top: 50px;
}
.menu-pop .menu-box .inner-5 {
  left: 10px;
  top: 70px;
}
.menu-pop .menu-box.menu-move-enter-active .inner {
  transform: translate3d(0, 0, 0);
  transition-timing-function: cubic-bezier(0, 0.57, 0.44, 1.97);
}
.menu-pop .menu-box.menu-move-enter-active .inner-1 {
  transition-delay: 0.1s;
}
.menu-pop .menu-box.menu-move-enter-active .inner-2 {
  transition-delay: 0.2s;
}
.menu-pop .menu-box.menu-move-enter-active .inner-3 {
  transition-delay: 0.3s;
}
.menu-pop .menu-box.menu-move-enter-active .inner-4 {
  transition-delay: 0.4s;
}
.menu-pop .menu-box.menu-move-enter-active .inner-5 {
  transition-delay: 0.5s;
}
.menu-pop .menu-box.menu-move-enter .inner,
.menu-pop .menu-box.menu-move-leave-active .inner {
  transition-timing-function: ease-in-out;
}
.menu-pop .menu-box.menu-move-enter .inner-1,
.menu-pop .menu-box.menu-move-leave-active .inner-1 {
  transform: translate3d(0, 60px, 0);
  transition-delay: 0.5s;
}
.menu-pop .menu-box.menu-move-enter .inner-2,
.menu-pop .menu-box.menu-move-leave-active .inner-2 {
  transform: translate3d(40px, 40px, 0);
  transition-delay: 0.4s;
}
.menu-pop .menu-box.menu-move-enter .inner-3,
.menu-pop .menu-box.menu-move-leave-active .inner-3 {
  transform: translate3d(60px, 0, 0);
  transition-delay: 0.3s;
}
.menu-pop .menu-box.menu-move-enter .inner-4,
.menu-pop .menu-box.menu-move-leave-active .inner-4 {
  transform: translate3d(40px, -40px, 0);
  transition-delay: 0.2s;
}
.menu-pop .menu-box.menu-move-enter .inner-5,
.menu-pop .menu-box.menu-move-leave-active .inner-5 {
  transform: translate3d(0px, -70px, 0);
  transition-delay: 0.1s;
}
.trade-view {
  padding: 18px 0px 18px 18px;
}
.trade-view > h1 {
  margin-bottom: 12px;
  line-height: 16px;
  font-size: 16px;
  color: #07111b;
}
.trade-view .trade-box {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}
.trade-view .trade-box > ul {
  overflow: auto;
}
.trade-view .trade-box > ul > li {
  display: inline-block;
  margin-right: 6px;
}
@-moz-keyframes move-in {
0% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
}
@-webkit-keyframes move-in {
0% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
}
@-o-keyframes move-in {
0% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
}
@keyframes move-in {
0% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
}
@-moz-keyframes move-out {
0% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
}
@-webkit-keyframes move-out {
0% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
}
@-o-keyframes move-out {
0% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
}
@keyframes move-out {
0% {
    background-color: rgba(7,17,27,0.8);
/*transform: scale(1);*/
    transform: translate3d(0px, 0px, 0px);
}
50% {
/*transform: scale(1.5);*/
    transform: translate3d(-100px, 0px, 0px);
}
100% {
    background-color: rgba(7,17,27,0);
/*transform: scale(0);*/
    transform: translate3d(460px, 0px, 0px);
}
}


